.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

.trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    transition: color 0.3s;
}

.trigger:hover {
    color: #1890ff;
}

.logo {
    height: 27px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px;
}

.login-form {
    max-width: 300px;
}

.login-form-forgot {
    float: right;
}

.login-form-button {
    width: 100%;
}

@media screen and (max-width:577px) {
    #head {
        height: 100px;
    }
    .header {
        height: 100px;
    }
}

@media screen and (max-width:577px) {
    #aPaciente {
        height: 100px;
    }
    .header {
        height: 100px;
    }
}

#buttonH span:hover {
    color: #fadb14;
}